import { motion } from 'framer-motion';

export default function Projects({ repos }) {
  const sortedByUpdated = [...repos].sort(
    (a, b) => new Date(b.updated_at) - new Date(a.updated_at)
  );
  const newRepos = sortedByUpdated.slice(0, 2);
  const popularRepos = repos.filter(repo => repo.stargazers_count > 20);

  return (
    <section id="projects" className="section projects-section">
      <div className="section-title-container">
        <motion.h2
          initial={{ opacity: 0, y: -20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          className="section-title-pill" 
        >
          New Repositories
        </motion.h2>
      </div>
      <div className="repo-list">
        {newRepos.map(repo => (
          <motion.div
            key={`${repo.id}-${repo.name}`}
            className="repo-card neon-card"
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            whileHover={{ scale: 1.02 }}
            transition={{ duration: 0.3 }}
          >
            <h3 style={{ fontSize: 'clamp(1.1rem, 3vw, 1.25rem)', wordWrap: 'break-word' }}>
              <a href={repo.html_url} target="_blank" rel="noopener noreferrer">{repo.name}</a>
            </h3>
            <p style={{ fontSize: 'clamp(0.9rem, 2.5vw, 0.95rem)' }}>
              {repo.description || 'No description available.'}
            </p>
            <div className="repo-meta" style={{ flexWrap: 'wrap', gap: '8px' }}>
              <span>⭐ {repo.stargazers_count}</span>
              <span>🕒 {new Date(repo.updated_at).toLocaleDateString()}</span>
              {repo.language && <span className="badge">{repo.language}</span>}
            </div>
          </motion.div>
        ))}
      </div>

      <div className="section-title-container">
        <motion.h2
          initial={{ opacity: 0, y: -20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          className="section-title-pill" 
        >
          Most Starred
        </motion.h2>
      </div>
      <div className="repo-list">
        {popularRepos.map(repo => (
          <motion.div
            key={`${repo.id}-${repo.name}`}
            className="repo-card neon-card"
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            whileHover={{ scale: 1.02 }}
            transition={{ duration: 0.3 }}
          >
            <h3 style={{ fontSize: 'clamp(1.1rem, 3vw, 1.25rem)', wordWrap: 'break-word' }}>
              <a href={repo.html_url} target="_blank" rel="noopener noreferrer">{repo.name}</a>
            </h3>
            <p style={{ fontSize: 'clamp(0.9rem, 2.5vw, 0.95rem)' }}>
              {repo.description || 'No description available.'}
            </p>
            <div className="repo-meta" style={{ flexWrap: 'wrap', gap: '8px' }}>
              <span>⭐ {repo.stargazers_count}</span>
              <span>🕒 {new Date(repo.updated_at).toLocaleDateString()}</span>
              {repo.language && <span className="badge">{repo.language}</span>}
            </div>
          </motion.div>
        ))}
      </div>
    </section>
  );
}
