import { useState, useEffect } from 'react';
import Header from '../components/Header';
import Logo from '../components/Logo';
import CryptoPrices from '../components/CryptoPrices';
import Hero from '../components/Hero';
import About from '../components/About';
import Projects from '../components/Projects';
import Contact from '../components/Contact';
import Footer from '../components/Footer';

export default function Home() {
  const [repos, setRepos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isHome, setIsHome] = useState(true);

  // Logika untuk fetch repos dari GitHub
  useEffect(() => {
    fetch('https://api.github.com/users/cmalf/repos?sort=updated&per_page=100')
      .then(response => {
        if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
        return response.json();
      })
      .then(data => {
        setRepos(data);
        setLoading(false);
      })
      .catch(err => {
        setError(err.message);
        setLoading(false);
      });
  }, []);

  // Logika untuk mendeteksi section home (jika menggunakan scroll)
  useEffect(() => {
    const heroRef = document.getElementById('home-section'); // Pastikan ID ini ada di komponen Hero
    if (!heroRef) return;
    const observer = new IntersectionObserver((entries) => {
      setIsHome(entries[0].isIntersecting);
    }, { threshold: 0.5 });
    observer.observe(heroRef);
    return () => observer.disconnect();
  }, []);

  if (loading) {
    return <div className="app-loading">Loading Portfolio...</div>;
  }

  if (error) {
    return <div className="app-error">Error: {error}</div>;
  }

  return (
    <div className="app">
      <Logo />
      <CryptoPrices isHome={isHome} />
      <main id="home-section"> {/* Tambahkan ID ini di main atau Hero */}
        <Hero />
        <About />
        <Projects repos={repos} />
        <Contact />
      </main>
      <Header /> 
      <Footer />
    </div>
  );
}
