import { motion } from 'framer-motion';
import { Link } from 'react-scroll';  // Import untuk smooth scroll

export default function Logo() {
  return (
    <Link
      to="home"  // Target #home (Hero section)
      spy={true}
      smooth={true}
      offset={-70}  // Adjust untuk fixed logo/nav (top space)
      duration={500}  // Smooth animasi sama seperti nav
      className="logo-link"  // Opsional: Custom class untuk style jika perlu
    >
      <motion.div
        className="logo-container neon-card"
        initial={{ opacity: 0, x: -20 }}
        animate={{ opacity: 1, x: 0 }}
        transition={{ duration: 0.6, delay: 0.2 }}
        whileHover={{ scale: 1.02 }}  // Hover scale via motion (sudah ada dari CSS)
        whileTap={{ scale: 0.98 }}  // Subtle press down on click
      >
        <video
          src="/img/ReactJs.webm"  // Path relative ke public/ – pindah file ke public/icon/ jika src/
          autoPlay
          loop
          muted
          playsInline
          className="logo-icon"
          title="Node.js Animated Icon"
        />
        <p className="logo-text neon-text-subtle">The Logic is My Muse</p>
      </motion.div>
    </Link>
  );
}
