import React from 'react';
import { useLocation, Link, Outlet } from 'react-router-dom';
import { motion } from 'framer-motion'; // Untuk animasi dock
import Logo from './Logo';
import Footer from './Footer';
import '../App.css'; // Untuk style .bottom-nav.dock-style
import '../styles/Blog.css'; // Untuk style blog

// Import icon 'Home' dari path yang ada di Header.jsx
import homeIcon from '/src/icon/homepageIcon.webp';

// Varian animasi untuk icon di dock
const iconMotion = {
  whileHover: { 
    scale: 1.2,
    y: -5,
    transition: { type: "spring", stiffness: 300, damping: 20 }
  }
};

const Layout = () => {
  const location = useLocation();
  // Cek apakah kita di halaman list blog (/blog)
  const isBlogListPage = location.pathname === '/blog';

  return (
    <div className="app">
      <Logo /> {/* Logo selalu tampil */}
      
      <main className="main-content">
        {/* Outlet akan me-render halaman (Blog atau BlogPost) */}
        <Outlet /> 
      </main>
      
      <Footer /> {/* Footer selalu tampil */}

      {/* Dock Minimalis Khusus Blog */}
      <nav className="bottom-nav dock-style">
        <div className="nav-container">
          
          {/* 1. Tombol Home (Selalu tampil di blog) */}
          <motion.div className="nav-item" {...iconMotion}>
            <Link to="/" className="nav-link" title="Home">
              <img src={homeIcon} alt="Home" className="nav-icon dock-icon" />
            </Link>
          </motion.div>

          {/* 2. Tombol Back (Hanya tampil di HALAMAN POST /blog/:slug) */}
          {!isBlogListPage && (
            <motion.div className="nav-item" {...iconMotion}>
              <Link to="/blog" className="nav-link" title="Kembali ke Blog">
              <img src="/icons/takemehome.webp" alt="Blog" className="nav-icon dock-icon" />
              </Link>
            </motion.div>
          )}

        </div>
      </nav>
    </div>
  );
};

export default Layout;