import React, { useState, useEffect, useRef } from 'react';
import BitcoinIcon from '/src/icon/bitcoin.webp';
import EthereumIcon from '/src/icon/ethereum.webp';
import SolanaIcon from '/src/icon/solana.svg';
import BnbIcon from '/src/icon/bnb.svg';

const COINGECKO_API = 'https://api.coingecko.com/api/v3/simple/price';
const CRYPTOS = [
  { id: 'bitcoin', symbol: 'BTC', icon: BitcoinIcon },
  { id: 'ethereum', symbol: 'ETH', icon: EthereumIcon },
  { id: 'solana', symbol: 'SOL', icon: SolanaIcon },
  { id: 'binancecoin', symbol: 'BNB', icon: BnbIcon }
];
const INTERVAL_MS = 30000;  // 30s

export default function CryptoPrices({ isHome = true }) {  // Default true untuk test
  const [prices, setPrices] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const intervalRef = useRef(null);

 // Fallback test data (uncomment untuk debug jika API fail)
  // const fallbackPrices = {
  //   bitcoin: { usd: 65000 },
  //   ethereum: { usd: 2500 },
  //   solana: { usd: 150 },
  //   binancecoin: { usd: 580 }
  // };

  const fetchPrices = async () => {
    console.log('Fetching crypto prices...');  // Debug log
    try {
      setLoading(true);
      setError(null);
      const ids = CRYPTOS.map(c => c.id).join(',');
      const response = await fetch(`${COINGECKO_API}?ids=${ids}&vs_currencies=usd`);
      console.log('API Response status:', response.status);  // Debug
      if (!response.ok) throw new Error(`API error: ${response.status}`);
      const data = await response.json();
      console.log('API Data:', data);  // Debug full response
      setPrices(data);
      //setPrices(fallbackPrices);  // Uncomment untuk test fallback
    } catch (err) {
      console.error('Crypto fetch error:', err);  // Console detail
      setError(err.message);
      //setPrices(fallbackPrices);  // Fallback on error untuk show something
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (isHome) {
      console.log('isHome true, starting fetch');  // Debug
      fetchPrices();  // Initial
      intervalRef.current = setInterval(fetchPrices, INTERVAL_MS);
      return () => {
        if (intervalRef.current) clearInterval(intervalRef.current);
      };
    } else {
      //console.log('isHome false, hiding widget');  // Debug
      setPrices({});
      setLoading(true);
    }
  }, [isHome]);

  if (!isHome) return null;  // Hide non-home

  const renderItem = (crypto) => {
    const price = prices[crypto.id]?.usd;
    if (loading || !price) return null;  // No show empty

    return (
      <div key={crypto.id} className="crypto-item">
        <img src={crypto.icon} alt={crypto.symbol} className="crypto-icon" />
        <span className="crypto-symbol">{crypto.symbol}:</span>
        <span className="crypto-price">${price.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</span>
      </div>
    );
  };

  // Show loading jika masih fetch
  if (loading) {
    return (
      <div className="crypto-widget">
        <div className="crypto-loading">Fetching...</div>
      </div>
    );
  }

  // Error show brief
  if (error) {
    return (
      <div className="crypto-widget">
        <div className="crypto-error">{error}</div>
      </div>
    );
  }

  return (
    <div className="crypto-widget">
      {CRYPTOS.map(renderItem)}
    </div>
  );
}
