import { Link } from 'react-scroll';
import { Link as Router } from 'react-router-dom';
import { motion } from 'framer-motion';  // Untuk bounce hover

import homeIcon from '/src/icon/homepageIcon.webp';
import aboutIcon from '/src/icon/aboutIconD.webp';
import projectsIcon from '/src/icon/projectsIcon.webp';
import contactIcon from '/src/icon/contactIcon.webp';

const navItems = [
  { id: 'home', label: 'Home', icon: homeIcon }, 
  { id: 'about', label: 'About', icon: aboutIcon }, 
  { id: 'projects', label: 'Projects', icon: projectsIcon }, 
  { id: 'contact', label: 'Contact', icon: contactIcon }, 
];

<nav>
  <Router to="/">Home</Router>
  <Router to="/blog">Blog</Router>
</nav>

export default function Header() {
  return (
    <nav className="bottom-nav dock-style">
      <div className="nav-container">
        {navItems.map(({ id, icon }) => (
          <motion.div  // Wrap untuk animasi
            key={id}
            className="nav-item"
            whileHover={{ 
              scale: 1.2,  // Magnification seperti dock
              y: -5,  // Subtle bounce up
              transition: { type: "spring", stiffness: 300, damping: 20 }
            }}
          >
            <Link
              to={id}
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
              className="nav-link"
              activeClass="active"
            >
              <img src={icon} alt={id} className="nav-icon dock-icon" />
            </Link>
          </motion.div>
        ))}
      </div>
    </nav>
  );
}
